$body-color: #404142;

* {
    box-sizing: border-box;
}

body {
    color: $body-color;
    font-family: 'Dosis', sans-serif;
    font-size: 16px;
    letter-spacing: .8px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 200;
    line-height: 1.1;
    margin-bottom: 10px;
    margin-top: 10px;
}

h2 {
    font-size: 30px;
}

h3 {
    font-size: 24px;
}

h4 {
    font-size: 18px;
}

p {
    margin: 0 0 10px;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    transition: all .3s;

    &,
    &:hover,
    &:focus,
    &:active,
    &:visited {
        text-decoration: none;
    }
}
